<template>
  <div>
    <div>
      <el-link
        :underline="false"
        type="primary"
        style="
          font-size: 16px;
          cursor: pointer;
          float: right;
          margin-bottom: 10px;
        "
        @click="add"
        ><i class="iconfont iconxinjian" style="padding-right: 5px"></i
        >新增</el-link
      >
    </div>

    <!-- <div style="margin-bottom:10px;">
        <span
          >已选择<span style="color: rgb(0, 161, 145); padding: 0 5px">{{
            tableSelection.length
          }}</span
          >项</span
        >
        <el-link
          class="table_link"
          type="danger"
          v-if="tableSelection.length"
          :underline="false"
          @click="handleMultiDelete"
          style="margin: -5px 10px 0px 10px"
          >批量删除</el-link
        >
      </div> -->
    <el-table
      v-loading="loading"
      ref="multipleTable"
      :data="tableData"
      size="small"
      border
      :height="height - 60"
      style="width: 100%; text-align: center; line-height: 18px"
    >
      <!-- <el-table-column
          type="selection"
          width="55"
          align="center"
        ></el-table-column> -->
      <el-table-column
        label="角色"
        align="center"
        prop="name"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column label="标识" align="center" prop="sign">
      </el-table-column>
      <el-table-column label="权限" align="center" width="300">
        <template slot-scope="scope">
          <el-checkbox-group v-model="scope.row.authority">
            <el-checkbox label="add" disabled>新增</el-checkbox>
            <el-checkbox label="edit" disabled>编辑</el-checkbox>
            <el-checkbox label="delete" disabled>删除</el-checkbox>
          </el-checkbox-group>
        </template>
      </el-table-column>

      <el-table-column
        label="账号"
        align="center"
        prop="account"
        show-overflow-tooltip
      ></el-table-column>

      <el-table-column
        label="密码"
        width="200"
        align="center"
        prop="password"
        show-overflow-tooltip
      ></el-table-column>

      <el-table-column label="操作" align="center" width="140">
        <template slot-scope="{ row }">
          <i
            class="el-icon-edit"
            style="margin-right: 20px"
            @click="handleEdit(row)"
          ></i>
          <i
            class="el-icon-delete"
            style="margin-right: 0"
            @click="handleDelete(row)"
          ></i>
        </template>
      </el-table-column>
      <template slot="empty">
        <div>
          <i
            class="iconfont iconzanwuziyuan"
            style="font-size: 40px; color: #8c8c8c"
          >
            <p style="font-size: 14px; margin-top: -30px">暂无资源</p>
          </i>
        </div>
      </template>
    </el-table>

    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogFormVisible"
      width="650px"
      :close-on-click-modal="false"
      :append-to-body="true"
      @closed="resetForm"
    >
      <el-form :model="form" :rules="rules" ref="ruleForm" size="mini">
        <el-form-item label="角色名字:" prop="name">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="标识:" prop="sign">
          <el-input v-model="form.sign" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="账号:" prop="account">
          <el-input v-model="form.account" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="密码:" prop="password">
          <el-input v-model="form.password" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="权限:" prop="authority">
          <el-checkbox-group v-model="form.authority">
            <el-checkbox label="add">新增</el-checkbox>
            <el-checkbox label="edit">编辑</el-checkbox>
            <el-checkbox label="delete">删除</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelForm" size="mini">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')" size="mini"
          >提 交</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "authority_cop",
  components: {},
  props: {
    height: {
      type: Number,
    },
  },
  data() {
    return {
      loading: false,
      tableData: [],
      dialogFormVisible: false,
      dialogTitle: "管理",
      id: null,
      form: {
        name: "",
        sign: "",
        account: "",
        password: "",
        authority: [],
      },
      rules: {
        name: [
          { required: true, message: "请输入名称", trigger: "blur" },
          {
            min: 1,
            max: 32,
            message: "长度在 1 到 32 个字符",
            trigger: "blur",
          },
        ],
        sign: [
          { required: true, message: "请输入", trigger: "blur" },
          {
            min: 1,
            max: 32,
            message: "长度在 1 到 32 个字符",
            trigger: "blur",
          },
        ],
        account: [
          { required: true, message: "请输入", trigger: "blur" },
          {
            min: 1,
            max: 32,
            message: "长度在 1 到 32 个字符",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入", trigger: "blur" },
          {
            min: 1,
            max: 32,
            message: "长度在 1 到 32 个字符",
            trigger: "blur",
          },
        ],
      },
    };
  },
  created() {},
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.$reqGet("/authority/allData").then((res) => {
        if (res.code == 200) {
          res.data.forEach((item) => {
            item.authority = [];
            if (item.add) item.authority.push("add");
            if (item.edit) item.authority.push("edit");
            if (item.delete) item.authority.push("delete");
          });
          this.tableData = res.data;
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.form.add = false;
          this.form.edit = false;
          this.form.delete = false;
          this.form.authority.forEach((item) => {
            this.form[item] = true;
          });
          let url = this.id ? "authority/edit" : "/authority/add";
          this.$reqPost(url, {
            ...this.form,
            id: this.id,
          }).then((res) => {
            if (res.code == 200) {
              if (res.data) this.dialogFormVisible = false;
              this.getData();
            } else {
              this.$message.error(res.msg);
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    cancelForm() {
      this.dialogFormVisible = false;
    },
    resetForm() {
      this.$refs["ruleForm"].resetFields();
      this.form = {
        name: "",
        sign: "",
        account: "",
        password: "",
        authority: [],
      };
      this.id = null;
      this.getData();
    },

    add() {
      this.dialogFormVisible = true;
    },
    handleEdit(row) {
      let data = JSON.parse(JSON.stringify(row));
      this.form = data;
      this.dialogFormVisible = true;
      this.id = data.id;
    },

    handleDelete(row) {
      this.$confirm("确定删除?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        closeOnClickModal: false,
      }).then(() => {
        let id = row.id;
        this.$reqPost("/authority/del", {
          id: id,
        }).then((res) => {
          if (res.code == 200) {
            this.getData();
          } else {
            this.$message.error(res.msg);
          }
        });
      });
    },
  },
};
</script>
<style scoped></style>
